body {
  width: 100%;
  height: 100%;
  margin: none;
  background-color: white;
}

#view {
  display: grid;
  gap: 0.3rem;
  margin: 1.5vw;
  grid-template-rows: auto auto minmax(55px, 75px);
}

#app {
  height: 100%;
  width: 100%;
}

.sm_icon {
  color: rgba(0, 0, 0, 0.75);
}

.sm_icon:hover {
  color: rgba(0, 0, 0, 0.9);
}

.sm_icon:visited {
  color: rgba(0, 0, 0, 0.75);
}

.section-title {
  text-align: center;
  font-weight: 700;
  font-style: normal;
  margin: 0px;
  font-size: 1.5em;
}

.section-subtitle {
  text-align: center;
  font-weight: 500;
  margin: 0px 0px 5px 0px;
  font-size: 1.3em;
  color: rgba(0, 0, 0, 0.75);
}

.section-info {
  text-align: center;
  margin: 0;
  font-size: 1.1em;
}

#info-text {
  display: grid;
  gap: 0rem;
  grid-template-rows: repeat(3, auto);
  justify-content: center;
  justify-items: center;
  vertical-align: center;
  align-items: center;
}

#info-layout {
  display: grid;
  gap: 0rem;
  grid-template-columns: repeat(2, auto);
  justify-content: center;
  justify-items: center;
  vertical-align: center;
  align-items: center;
}

#info-name {
  cursor: default;
  font-weight: 800;
  padding: 0px;
  padding: 0px;
  color: rgba(0, 0, 0, 0.8);
  font-size: 1.7rem;
  margin: 0px 0px 0px 2vw;
  align-self: center;
}

#info-degree {
  margin: 0px;
  color: rgba(0, 0, 0, 0.7);
  padding: 0px;
  font-size: 1rem;
  font-weight: 600;
}

#info-position {
  margin: 0px;
  color: rgba(0, 0, 0, 0.6);
  padding: 0px;
  font-size: 0.9rem;
  font-weight: 500;
}

/* TODO Find good icon-list placement */
#info-icon-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: none;
}

.info-icon {
  display: inline;
  margin-right: 12px;
}

.outer-img {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
}

#content-grid {
  display: grid;
  grid-template-rows: auto auto auto auto;
  justify-content: start;
}

.inner-img {
  border-radius: 50%;
  width: 45px;
  height: 45px;
}

#side-nav {
  display: grid;
  grid-template-rows: auto auto 100%;
  gap: 0.5rem;
  max-width: 300px;
}

#nav-opts {
  background-color: blue;
  justify-content: center;
}

/* TODO Landscape mobile view? */
/* 700px+ */
@media (min-width: 600px) {
  /* TODO This will be the concatenated side view. [minNav|        content        ] */
  .section-title {
    font-size: 3em;
  }

  .section-subtitle {
    font-size: 2em;
  }

  .section-info {
    font-size: 1.37em;
  }
  #info-name {
    font-weight: 800;
    margin: 0px 0px 0px 2vw;
    font-size: 2.3rem;
  }

  #view {
    /* grid-template-rows: minmax(45px, 55px) auto minmax(45px,55px); */
  }
}

@media (min-width: 900px) {
  .section-title {
    text-align: start;

    font-size: 3em;
  }

  .section-subtitle {
    text-align: start;

    font-size: 2em;
  }

  .section-info {
    text-align: start;

    font-size: 1.37em;
  }
  #info-name {
    font-weight: 800;
    margin: 0px 0px 0px 2vw;
    font-size: 1.5rem;
  }

  #info-degree {
    font-size: 1rem;
    font-weight: 600;
  }

  #info-position {
    font-size: 0.9rem;
    font-weight: 500;
  }

  /* TODO Maybe using React/JS in the side nav... window.size() < 90 ? minNav : regNav */
  #view {
    grid-template-columns: minmax(200px, 300px) auto;
    margin: 2.5vh 5vw 0.5vh 2.5vw;
  }

  #info-layout {
    grid-template-columns: none; /* Didn't realize rows stayed active */
    grid-template-rows: repeat(5, auto);
    justify-content: center;
    justify-items: center;
  }

  .outer-img {
    width: 162px;
    height: 162px;
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
  }

  .inner-img {
    width: 150px;
    height: 150px;
  }

  #info-name {
    display: block;
    font-size: 1.35rem;
    margin: 4px 0px 0px 0px;
  }

  #info-position {
    margin: 2px 0px 6px 0px;
    display: block;
  }

  #info-degree {
    margin: 2px 0px 0px 0px;
    display: block;
  }

  #info-icon-list {
    display: block;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media (min-width: 1200px) {
  #view {
    display: grid;
    grid-template-columns: minmax(200px, 300px) auto;
    gap: 1rem;
    margin: 2.5vh 5vw 0.5vh 2.5vw;
  }

  .section-title {
    font-weight: 700;
    font-style: normal;
    margin: 0px;
    font-size: 3em;
  }

  .section-subtitle {
    font-weight: 500;
    margin: 0px 0px 5px 0px;
    font-size: 2em;
    color: rgba(0, 0, 0, 0.75);
  }

  .section-info {
    margin: 0;
    font-size: 1.37em;
  }

  #info-layout {
    display: grid;
    grid-template-rows: repeat(5, auto);
    justify-content: center;
    justify-items: center;
    gap: 0rem;
  }

  #info-name {
    cursor: default;
    font-weight: 800;
    padding: 0px;
    font-size: 1.35rem;
    margin: 4px 0px 0px 0px;
    padding: 0px;
    color: rgba(0, 0, 0, 0.75);
  }

  #info-degree {
    color: rgba(0, 0, 0, 0.7);
    padding: 0px;
    font-size: 1rem;
    font-weight: 600;
  }

  #info-position {
    color: rgba(0, 0, 0, 0.6);
    padding: 0px;
    font-size: 0.9rem;
    font-weight: 500;
  }

  #info-icon-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }

  .info-icon {
    display: inline;
    margin-right: 12px;
  }

  .outer-img {
    background: rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 162px;
    height: 162px;
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
  }

  #content-grid {
    display: grid;
    grid-template-rows: auto auto auto auto;
    justify-content: start;
    justify-items: start;
  }

  .inner-img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
  }

  #side-nav {
    display: grid;
    grid-template-rows: auto auto 100%;
    gap: 0.5rem;
    max-width: 300px;
  }
}
